import {
  FaReact as ReactIcon,
  FaFigma as Figma,
  FaSass as SASS,
} from "react-icons/fa";
import { SiRedux as Redux, SiStyledcomponents as Styled } from "react-icons/si";

const icons = [
  {
    name: "React",
    icon: () => <ReactIcon />,
  },
  {
    name: "Redux",
    icon: () => <Redux />,
  },
  {
    name: "Styled-components",
    icon: () => <Styled />,
  },
  {
    name: "Figma",
    icon: () => <Figma />,
  },
  {
    name: "SASS",
    icon: () => <SASS />,
  },
];

export default icons;
